@import "../../global.scss";

.concepts{
	background-color: black;
	display: flex;
	flex-direction: column;
	align-items: center;
	height: 100%;    

	h1{
		z-index: 3;
		font-size: 40px;
		color: white;
		overflow: hidden;;
	}
	.container{
		margin-top: 100px;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;   

		.item {
			display: flex;
			overflow: hidden;
			width: 250px;
			height: 250px;
			border-radius: 20px;
			border: 1px solid rgb(240, 239, 239);
			align-items: center;
			
			label{
				z-index: 3;
				font-size: 40px;
				color: white;
			}

			img {
				width: 100%;
				z-index: 0;
				@include mobile{
					width: 30%;
				  }
			
				&:hover {
					// background-color: $mainColor;
					// filter: grayscale(0%);
					img {
						// opacity: 0.2;
						z-index: 0;
						width: 30%;
					}
				}
			}
		}
	}        
}